import React, { useState, useEffect, useMemo } from 'react';
import axios from 'axios';
import { useTable, usePagination } from 'react-table';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { API_BASE_URL } from './config';

function App() {
    const [customers, setCustomers] = useState([]);
    const [selectedCustomer, setSelectedCustomer] = useState(null);
    const [transactions, setTransactions] = useState([]);
    const [newCustomer, setNewCustomer] = useState({ name: '', email: '' });
    const [startDate, setStartDate] = useState('');
    const [endDate, setEndDate] = useState('');

    useEffect(() => {
        fetchCustomers();
    }, []);

    const fetchCustomers = async () => {
        try {
            const response = await axios.get(`${API_BASE_URL}/api/customers`);
            setCustomers(response.data);
        } catch (error) {
            console.error('Error fetching customers:', error);
            toast.error('');
        }
    };

    const fetchCustomerDetails = async (id) => {
        try {
            const response = await axios.get(`${API_BASE_URL}/api/customers/${id}`);
            setSelectedCustomer(response.data);
        } catch (error) {
            console.error('Error fetching customer details:', error);
            toast.error('');
        }
    };

    const fetchCustomerTransactions = async (id) => {
        try {
            if (!startDate || !endDate) {
                toast.warning('');
                return;
            }
            const response = await axios.get(`${API_BASE_URL}/api/customers/${id}/transactions`, {
                params: { start_date: startDate, end_date: endDate },
            });
            if (response.data === null) {
                console.log('No transactions found or error occurred');
                toast.info('');
                setTransactions([]);
            } else if (Array.isArray(response.data)) {
                if (response.data.length === 0) {
                    toast.info('');
                }
                setTransactions(response.data);
            } else {
                console.error('Unexpected response format:', response.data);
                toast.error('');
                setTransactions([]);
            }
        } catch (error) {
            console.error('Error fetching transactions:', error);
            toast.error('');
            setTransactions([]);
        }
    };

    const handleCreateCustomer = async () => {
        try {
            await axios.post(`${API_BASE_URL}/api/customers`, newCustomer);
            setNewCustomer({ name: '', email: '' });
            fetchCustomers();
            toast.success('');
        } catch (error) {
            console.error('Error creating customer:', error);
            toast.error('');
        }
    };

    const handleUpdateCustomer = async () => {
        try {
            await axios.put(`${API_BASE_URL}/api/customers/${selectedCustomer.customer.id}`, {
                name: selectedCustomer.customer.name,
                email: selectedCustomer.customer.email,
            });
            fetchCustomers();
            toast.success('');
        } catch (error) {
            console.error('Error updating customer:', error);
            toast.error('');
        }
    };

    const columns = useMemo(
        () => [
            {
                Header: '',
                accessor: 'name',
            },
            {
                Header: '',
                accessor: 'email',
            },
        ],
        []
    );

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        prepareRow,
        page,
        canPreviousPage,
        canNextPage,
        pageOptions,
        pageCount,
        gotoPage,
        nextPage,
        previousPage,
        setPageSize,
        state: { pageIndex, pageSize },
    } = useTable(
        {
            columns,
            data: customers,
            initialState: { pageIndex: 0, pageSize: 10 },
        },
        usePagination
    );

    return (
        <div className="App">
            <ToastContainer />
            <h1></h1>

            <h2></h2>
            <table {...getTableProps()} style={{ border: 'solid 1px blue' }}>
                <thead>
                {headerGroups.map(headerGroup => (
                    <tr {...headerGroup.getHeaderGroupProps()}>
                        {headerGroup.headers.map(column => (
                            <th
                                {...column.getHeaderProps()}
                                style={{
                                    borderBottom: 'solid 3px red',
                                    background: 'aliceblue',
                                    color: 'black',
                                    fontWeight: 'bold',
                                }}
                            >
                                {column.render('Header')}
                            </th>
                        ))}
                    </tr>
                ))}
                </thead>
                <tbody {...getTableBodyProps()}>
                {page.map(row => {
                    prepareRow(row)
                    return (
                        <tr {...row.getRowProps()} onClick={() => fetchCustomerDetails(row.original.id)}>
                            {row.cells.map(cell => {
                                return (
                                    <td
                                        {...cell.getCellProps()}
                                        style={{
                                            padding: '10px',
                                            border: 'solid 1px gray',
                                            background: 'papayawhip',
                                        }}
                                    >
                                        {cell.render('Cell')}
                                    </td>
                                )
                            })}
                        </tr>
                    )
                })}
                </tbody>
            </table>
            <div className="pagination">
                <button onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                    {'<<'}
                </button>{' '}
                <button onClick={() => previousPage()} disabled={!canPreviousPage}>
                    {'<'}
                </button>{' '}
                <button onClick={() => nextPage()} disabled={!canNextPage}>
                    {'>'}
                </button>{' '}
                <button onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
                    {'>>'}
                </button>{' '}
                <span>
                    Page{' '}
                    <strong>
                        {pageIndex + 1} of {pageOptions.length}
                    </strong>{' '}
                </span>
                <span>
                    | Go to page:{' '}
                    <input
                        type="number"
                        defaultValue={pageIndex + 1}
                        onChange={e => {
                            const page = e.target.value ? Number(e.target.value) - 1 : 0
                            gotoPage(page)
                        }}
                        style={{ width: '50px' }}
                    />
                </span>{' '}
                <select
                    value={pageSize}
                    onChange={e => {
                        setPageSize(Number(e.target.value))
                    }}
                >
                    {[10, 20, 30, 40, 50].map(pageSize => (
                        <option key={pageSize} value={pageSize}>
                            Show {pageSize}
                        </option>
                    ))}
                </select>
            </div>

            {selectedCustomer && (
                <div>
                    <h2></h2>
                    <input
                        value={selectedCustomer.customer.name}
                        onChange={(e) => setSelectedCustomer({
                            ...selectedCustomer,
                            customer: {...selectedCustomer.customer, name: e.target.value}
                        })}
                    />
                    <input
                        value={selectedCustomer.customer.email}
                        onChange={(e) => setSelectedCustomer({
                            ...selectedCustomer,
                            customer: {...selectedCustomer.customer, email: e.target.value}
                        })}
                    />
                    <button onClick={handleUpdateCustomer}></button>
                    <p>: {selectedCustomer.total_amount_last_year}</p>

                    <h3> - {selectedCustomer.customer.name} (ID: {selectedCustomer.customer.id})</h3>
                    <input
                        type="date"
                        value={startDate}
                        onChange={(e) => setStartDate(e.target.value)}
                    />
                    <input
                        type="date"
                        value={endDate}
                        onChange={(e) => setEndDate(e.target.value)}
                    />
                    <button onClick={() => fetchCustomerTransactions(selectedCustomer.customer.id)}>
                        
                    </button>
                    {transactions && transactions.length > 0 ? (
                        <ul>
                            {transactions.map(transaction => (
                                <li key={transaction.id}>
                                    {new Date(transaction.transaction_date).toLocaleDateString()} -
                                    {transaction.borrow_fee}
                                </li>
                            ))}
                        </ul>
                    ) : (
                        <p></p>
                    )}
                </div>
            )}

            <h2></h2>
            <input
                placeholder=""
                value={newCustomer.name}
                onChange={(e) => setNewCustomer({...newCustomer, name: e.target.value})}
            />
            <input
                placeholder=""
                value={newCustomer.email}
                onChange={(e) => setNewCustomer({...newCustomer, email: e.target.value})}
            />
            <button onClick={handleCreateCustomer}></button>
        </div>
    );
}

export default App;
